/* sass 문법
1. 변수에 데이터를 저장해서 쓰자
  $변수명: 변수에 넣을 값
2. @import 파일경로
3. 셀렉터 대신 쓰는 nesting
4. 복붙하지 말고 @extend
5. 함수는 @mixin/ @include */
$mainColor: #ff0000;
.red {
  color: $mainColor;
}

// 5. 함수는 @mixin/ @include
@mixin 함수() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

// 4. 복붙하지 말고 @extend
.my-alert2 {
  @extend .my-alert;
  background-color: #ffe591;
}

.my-alert p {
  margin-bottom: 0;
}
